/* layout */
.chat-app {
    /*height: 95vh;*/
    height: 97vh;

}

.chat-app_left {
    float: left;
    width: calc(35% - 1px);
    height: inherit;
    border-right: 1px solid grey;
}

.chat-app_right {
    float: left;
    width: 65%;
    height: inherit;
}

.heading {
    height: 60px;
    text-align: center;
    border-bottom: 1px solid rgba(0, 0, 0, .10);
}

/* thread */

.messenger-title {
    line-height: 60px;
    margin: 0;
}

.thread-list {

}

.thread-item {
    position: relative;
    heidht: 70px;
}

.thread-item_left {
    float: left;
    width: 50px;
    padding: 15px 10px;
}

.thread-item_right {
    float: left;
    width: calc(100% - 90px);
    padding: 20px 10px;
    border-bottom: 1px solid rgba(0, 0, 0, .10);
}

.thread-from {
    font-weight: 400;
    line-height: 1.4em;
    color: black;
}

.thread-content {
    color: rgba(0, 0, 0, .40);
    font-size: 14px;
}

.thread-time {
    color: rgba(0, 0, 0, .40);
    position: absolute;
    top: 23px;
    right: 13px;
    font-size: 14px;
}

.img-circle {
    border-radius: 50%;
}

/* message */

.current-target {
    line-height: 60px;
    font-size: 16px;
}

.message-list {
    padding-top: 10px;
    height: calc(100% - 127px);
    height: calc(100% - 118px);
    overflow-y: scroll;
    position: sticky;
}

.message-item {
    line-height: 2em;
    margin: 10px 0;
}

.footer {
    border-top: 1px solid rgba(0, 0, 0, .10);
    height: 50px;
}

.message-from-other {
    padding-left: 25px;
    text-align: left;
}

.message-from-me {
    padding-right: 25px;
    text-align: right;
}

.message-from-other span {
    display: inline-block;
    border-top-left-radius: 1.3em;
    border-top-right-radius: 1.3em;
    border-bottom-left-radius: 1.3em;
    border-bottom-right-radius: 1.3em;
    border: 1px solid rgba(0, 0, 0, .10);
    padding: 0 15px;
    color: black;
    background-color: rgba(255, 255, 255, .10);
}

.message-from-me span {
    display: inline-block;
    border-top-left-radius: 1.3em;
    border-top-right-radius: 1.3em;
    border-bottom-left-radius: 1.3em;
    border-bottom-right-radius: 1.3em;
    padding: 0 15px;
    color: white;
    background-color: #0084ff;
}

.new-message {
    width: 100%;
    height: 100%;
}

/* clearfix
 * http://stackoverflow.com/questions/211383/which-method-of-clearfix-is-best
 */
.clearfix:before,
.clearfix:after {
    content:"";
    display:table;
}
.clearfix:after {
    clear:both;
}
.clearfix {
    zoom:1; /* For IE 6/7 (trigger hasLayout) */
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
    cursor: pointer;
}